.movieListImage,
.movieListPoster {
  display: flex;
  margin-left: 50px;
  margin-top: 40px;
  overflow-x: scroll;
  gap: 10px;
  padding-bottom: 10px;
}
.movieListImage img {
  height: 120px;
  align-self: center;
  width: auto;
}
.movieListPoster img {
  height: 200px;
  align-self: center;
  width: auto;
}
.movieListPoster img:hover {
  animation: growPoster 0.3s linear;
  animation-fill-mode: forwards;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
::-webkit-scrollbar-track {
  background: black;
}
::-webkit-scrollbar-thumb {
  background: #2c2c2c;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
  background: #4e4e4e;
}
.movieListImage img:hover {
  animation: grow 0.3s linear;
  animation-fill-mode: forwards;
}
@keyframes grow {
  from {
    height: 120px;
    width: auto;
  }
  to {
    height: 130px;
    width: auto;
  }
}
@keyframes growPoster {
  from {
    height: 200px;
    width: auto;
  }
  to {
    height: 220px;
    width: auto;
  }
}

.categoryName {
  color: white;
  margin-left: 20px;
}
