.banner {
  width: 100%;
  background-size: cover;
  padding-top: 150px;
  padding-left: 20px;
  padding-bottom: 80px;
  box-sizing: border-box;
}
.banner h1 {
  color: white;
  font-size: 50px;
}
.bannerActions {
  display: flex;
  gap: 20px;
}
.bannerActions button {
  color: white;
  background-color: rgb(0, 0, 0, 0.5);
  border: none;
  font-size: large;
  padding: 10px 20px;
}
.banner p {
  color: white;
  font-weight: bold;
  width: 400px;
}
