.formInput svg {
  width: 25px;
  height: 25px;
}
.formContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.searchForm {
  background-color: white;
  margin-top: 100px;
  width: 30%;
  display: flex;
  gap: 20px;
  flex-direction: column;
}
.formInput {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  border-bottom: 2px rgb(0, 187, 255) solid;
}
.formInput input {
  width: 90%;
  border: none;
}
.formInput input:focus {
  outline: none;
}
.actions {
  display: flex;
  justify-content: flex-end;
  padding: 30px;
  padding-top: 20px;
}
.actions button {
  width: 80px;
  height: 30px;
  border: none;
  font-weight: bold;
}
.search {
  background-color: rgb(0, 187, 236);
  color: white;
}
.reset {
  background-color: transparent;
  color: grey;
}
