.movieDetail {
  display: flex;
  width: 100%;
  margin-left: 45px;
  margin-top: 40px;
  gap: 40px;
  box-sizing: border-box;
  color: white;
}
.movieDetail h1 {
  border-bottom: 1px solid white;
  padding-bottom: 20px;
}
.movieText {
  width: 45%;
}
.movieTrailer {
  width: 48%;
}
.movieTrailer iframe {
  border-radius: 20px;
  border: none;
}
